import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";
import Main from "./main";
import {cleanSL, initSellLeadersAC, requestItems, requestItemsFromCategories} from "../../redux/productsReducer";

let mapStateToProps = (state) => {

    return {
        slidesResult: state.SlidesPage.slidesResult,
        initSlider: state.SlidesPage.initSlider,
        initSellLeaders: state.SlidesPage.initSellLeaders
    }
}

export default compose(connect(mapStateToProps, {requestItemsFromCategories, initSellLeadersAC, cleanSL, requestItems}), withRouter) (Main);