import axios from "axios";

const ADD_LAST_PURCHASES = 'ADDLAST-PURCHASES';
const INIT_LAST_PURCHASES = 'INIT-LAST-PURCHASES';

export const addLastPurchases = (lastPur) => {
    return {
        type: ADD_LAST_PURCHASES,
        lastPur
    }
}

export const initLastPurchasesAC = (isBool) => {
    return {
        type: INIT_LAST_PURCHASES,
        isBool
    }
}


let initialState = {
 lastPurchases: [],
 initLastPurchases: false
}

const lastPurchasesReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_LAST_PURCHASES: return {
            ...state,
            lastPurchases: action.lastPur
        }
        case INIT_LAST_PURCHASES: return {
            ...state,
            initLastPurchases: action.isBool
        }

        default: return state
    }
}
const xmlBodyStr = `<digiseller.request>
  <seller>
    <id>549328</id>
    <uid>453CE5D199E44631AA97B9B4C8F2CCD2</uid>
  </seller>
  <group>false</group>
  <lang></lang>
</digiseller.request>`;

export const requestLastPurchases = () => async (dispatch) => {

        await axios.post('http://shop.digiseller.ru/xml/shop_last_sales.asp', xmlBodyStr).then(res => {
            console.log(res)
            let parseString = require('xml2js').parseString;
            parseString(res.data, function (err, result) {
                let resultObjs = Object.values(result)[0].sales[0].sale

                console.log(resultObjs.length)
                let lastPurArray = [];
                for (let i = 0; i < resultObjs.length && lastPurArray.length < 50; i++) {
                    let item = resultObjs[i].product[0];

                    if (lastPurArray.filter(itemFor => itemFor.id === item.id[0]).length === 0) {
                        lastPurArray.push({
                            id: item.id[0],
                            price: item.prices[0].rur[0],
                            imgItem: `https://graph.digiseller.ru/img.ashx?id_d=${item.id[0]}&w=210&h=145`
                        })
                    }

                }
                dispatch(addLastPurchases(lastPurArray))
                dispatch(initLastPurchasesAC(true))
            });
        })

}


export default lastPurchasesReducer;