import React from "react";
import {NavLink} from "react-router-dom";
import style from "./header.module.css";

const HeaderLiElement = ({link, text}) => {
    return (
        <li className="nav-item">
            <NavLink to={`/${link}`} className="nav-link" aria-current="page">
                <span className={style.linkHoverSolid}>{text}</span></NavLink>
        </li>
    )
}

export default HeaderLiElement;