import React, {useEffect, useState} from "react";
import ReactPaginate from "react-paginate";
import {GrFormPrevious, GrFormNext} from "react-icons/gr";
import style from "./paginator.module.css";

const Paginator = ({items, itemsPerPage, Items}) => {

  function PaginatedItems({ itemsPerPage}) {

    const [currentItems, setCurrentItems] = useState(items);
    const [pageCount, setPageCount] = useState(0);
    const [itemOffset, setItemOffset] = useState(0);


    useEffect(() => {
      const endOffset = itemOffset + itemsPerPage;
      setCurrentItems(items.slice(itemOffset, endOffset));
      setPageCount(Math.ceil(items.length / itemsPerPage));
    }, [itemOffset, itemsPerPage]);


    const handlePageClick = (event) => {
      const newOffset = (event.selected * itemsPerPage) % items.length;
      setItemOffset(newOffset);
    };

    return (
        <>
          <Items currentItems={currentItems}/>
          <div className={style.paginatorWrap}>
            <ReactPaginate
            breakLabel="..."
            nextLabel=<GrFormNext/>
            onPageChange={handlePageClick}
            pageRangeDisplayed={1}
            pageCount={pageCount}
            previousLabel=<GrFormPrevious/>
            pageLinkClassName={style.itemLink}
            previousLinkClassName={style.itemLink}
            nextLinkClassName={style.itemLink}
            containerClassName={style.paginator}
            activeLinkClassName={style.activeItem}
            renderOnZeroPageCount={null}
            />
          </div>
        </>
    );
  }

  return (
      <>
        {!!items && <PaginatedItems itemsPerPage={itemsPerPage} />}
      </>

  )
}

export default Paginator;