import React from "react";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import style from "./lastPurchases.module.css";
import {NavLink} from "react-router-dom";
import {GrPrevious} from "@react-icons/all-files/gr/GrPrevious";
import {GrNext} from "@react-icons/all-files/gr/GrNext";
import ItemBlock from "../ItemBlock/itemBlock";


const LastPurchases = (props) => {

    let deviceBool = (props.deviceType === "desktop" || "desktop2") ? true : false

    const ButtonGroup = ({ next, previous, arrows, ...rest }) => {
        const {
            carouselState: { currentSlide, totalItems, slidesToShow, deviceType }
        } = rest;

        console.log(deviceType)


        return  arrows ? <div className="container-lg center">

                  <button aria-label="Go to previous slide"
                    className={currentSlide === 0 ? "disable" : style.arrowPrev}
                    onClick={() => previous()}><GrPrevious /></button>
                    <button aria-label="Go to next slide"
                    className={currentSlide === totalItems - slidesToShow ? "disable" : style.arrowNext}
                    onClick={() => next()}><GrNext /></button>

            </div> : <div></div>


    };

    return (
<div className="container-lg center">
<div className={style.titleLastPur}>
    <div className={style.titleLastPurText}>ПОСЛЕДНИЕ ПРОДАЖИ</div>
</div>
    <div className={style.carouselWrap}>

        <Carousel
            renderButtonGroupOutside
            additionalTransfrom={0}
            draggable
            focusOnSelect={false}
            infinite
            minimumTouchDrag={5}
            pauseOnHover
            rewind={false}
            rewindWithAnimation={false}
            rtl={false}
            shouldResetAutoplay
            slidesToSlide={1}
            swipeable
            centerMode={true}
            autoPlay
            containerClass={style.containerCarousel}
            autoPlaySpeed={2300}
            arrows={false}
            customButtonGroup={ <ButtonGroup
                next={props.next}
                previous={props.previous}
                arrows={deviceBool}
                rest={props.rest}
            />}
            responsive={{
                desktop: {
                    breakpoint: {
                        max: 3000,
                        min: 1399
                    },
                    items: 4,

                },
                desktop2: {
                    breakpoint: {
                        max: 1398,
                        min: 1024
                    },
                    items: 3,

                },
                tablet: {
                    breakpoint: {
                        max: 1023,
                        min: 933
                    },
                    items: 4,

                },
                tablet2: {
                    breakpoint: {
                        max: 932,
                        min: 768
                    },
                    items: 3,

                },
                mobile: {
                    breakpoint: {
                        max: 767,
                        min: 558
                    },
                    items: 2,

                },
                mobile2: {
                    breakpoint: {
                        max: 557,
                        min: 0
                    },
                    items: 1,

                }
            }}
        >
            {props.lastPurchases.map(item =>
                <ItemBlock key={item.id} item={item}/>
            )}

        </Carousel>

    </div>

</div>
    )
}

export default LastPurchases;