import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";
import {initLastPurchases, requestLastPurchases} from "../../../redux/lastPurchasesReducer";
import LastPurchases from "./lastPurchases";


let mapStateToProps = (state) => {
    return {
        lastPurchases: state.LastPurchasesPage.lastPurchases,
        initLastPurchases: state.LastPurchasesPage.initLastPurchases
    }
}

export default compose(connect(mapStateToProps, {requestLastPurchases}), withRouter) (LastPurchases);