import React from "react";
import style from "./item.module.css";

const ItemNoAvailable = (props) => {
    return (
        <div className="container-lg">
            <span className={style.notAvailable}>Товар временно не доступен.<br/>
                Приносим свои извинения!</span>
        </div>
    )
}

export default ItemNoAvailable;