import React from "react";
import style from "./footer.module.css";
import {SiWebmoney} from "@react-icons/all-files/si/SiWebmoney";
import {FaBitcoin} from "@react-icons/all-files/fa/FaBitcoin";
import {SiLitecoin} from "@react-icons/all-files/si/SiLitecoin";
import {FaSteam} from "@react-icons/all-files/fa/FaSteam";
import {BsFillCreditCard2BackFill} from "react-icons/all";
import {FaEthereum} from "@react-icons/all-files/fa/FaEthereum";
import {FaCcMastercard} from "@react-icons/all-files/fa/FaCcMastercard";
import {SiQiwi} from "@react-icons/all-files/si/SiQiwi";
import {FaPaypal} from "@react-icons/all-files/fa/FaPaypal";

const ItemFooter = ({text}) => {
  return <div className="col-sm">
      <div className={style.itemFooter}>
          <span className={style.itemTextFooter}>{text}</span>
      </div>
  </div>
}

const Footer = (props) => {
    return (
        <div className={style.footerWrap}>

            <div className="container-lg center">
                <div className="row">
                    <div className="col-sm">
                        <div className="row">
                            <ItemFooter text={"Лучшая цена"} />
                            <ItemFooter text={"Гарантия 100%"} />
                        </div>

                        <div className="row">
                            <ItemFooter text={"Все способы оплаты"} />
                            <ItemFooter text={"Тысячи положительных отзывов"} />
                        </div>
                    </div>

                    <div className="col-sm">
                        <div className="row">
                            <ItemFooter text={"Моментальная доставка"} />
                            <ItemFooter text={"Техническая поддержка"} />
                        </div>
                        <div className="row">
                            <ItemFooter text={"Регулярные акции и скидки"} />
                            <ItemFooter text={"Более 200000+ продаж"} />
                        </div>
                    </div>
                </div>

                <div style={{
                    marginTop: '20px'
                }} className="row">
                    <div className="col-lg">
                        <span className={style.descriptionInfoFooter}>
                            Сайт работает для всех стран, включая СНГ и Россию. Некоторые товары имеют региональные ограничения,
                        которые указаны в описании товара или при оформлении заказа, обратите на это внимание.
                        Все продаваемые товары на площадке представлены проверенными продавцами, которые прошли верификацию и имеют персональный аттестат.
                        Мы ориентируемся на качество и ведем систематическую проверку продавцов и их товара.
                        </span>

                    </div>
                    <div className="col-lg">
                        <h4>Полезная информация</h4>
                        <div className="row">
                            <div className="col">
                                <span className={style.infoFooterItem}>Связь с поддержкой</span>
                                <span className={style.infoFooterItem}>Как купить</span>
                                <span className={style.infoFooterItem}>Гарантии</span>
                            </div>
                            <div className="col">
                                <span className={style.infoFooterItem}>Группа ВК</span>
                                <span className={style.infoFooterItem}>Сотрудничество</span>
                            </div>
                        </div>

                    </div>

                </div>


                <div className={style.footerLogo}>
                    <h1>LOGO</h1>
                    <span className={style.footerInfoUnderLogo}>Все упомянутые товарные знаки, названия игр и компаний, логотипы, материалы являются
                собственностью соответствующих владельцев.</span>
                </div>

            <div className={style.paymentWrap}>
                <SiWebmoney/>
                <FaBitcoin/>
                <SiLitecoin/>
                <FaSteam/>
                <BsFillCreditCard2BackFill />
                <FaEthereum/>
                <FaCcMastercard/>
                <SiQiwi/>
                <FaPaypal/>
            </div>

            </div>



        </div>
    )
}

export default Footer;