import React, { useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import SlideItem from "./slideItem";
import style from "./slider.module.css";

function Slider({slidesResult}) {

    const [index, setIndex] = useState(0);

    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };

    return <div className={style.wrapSlider}>
        <Carousel activeIndex={index} onSelect={handleSelect} controls={false}>

            {slidesResult.map(slide =>  <Carousel.Item key={slide[0].id}>

                    <div style={{
                        paddingBottom: "65px"
                    }} className="container">
                        <div className="row">
                            <div className="col-lg-6">
                                <SlideItem dataItem={slide[0]} classNameItem={style.firstTypeSlideBlock}/>
                            </div>
                            <div className="col-lg-6">
                                <div className="row">
                                    <div className="col-6 col-lg-6">
                                        <SlideItem dataItem={slide[1]}
                                                   classNameItem={style.secondTypeSlideBlock}/>
                                        <SlideItem dataItem={slide[2]} classNameItem={style.secondTypeSlideBlock2}/>
                                    </div>
                                    <div className="col-6 col-lg-6">
                                        <SlideItem dataItem={slide[3]} classNameItem={style.secondTypeSlideBlock}/>
                                        <SlideItem dataItem={slide[4]} classNameItem={style.secondTypeSlideBlock2}/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Carousel.Item>
            )}
        </Carousel>
        </div>
}

export default Slider;