import React from "react";
import {Vortex} from 'react-loader-spinner';
import style from './preloader.module.css';

const PreloaderComponent = ({w, h, main = false, ...props}) => {
    return (

        <div className={main === false ? style.preloaderWrap : style.preloaderWrapMain}>
            <Vortex
                visible={true}
                height={h}
                width={w}
                ariaLabel="vortex-loading"
                wrapperStyle={{}}
                wrapperClass="vortex-wrapper"
                colors={['#ffdf71', '#363636', '#ffca09', '#111111', '#363636', '#ffdf71']}
            />
        </div>
    )
}

export default PreloaderComponent;