import React, {useEffect, useState} from "react";
import style from "./header.module.css";
import {NavLink} from "react-router-dom";
import {IoCart} from "react-icons/io5";
import {FaSteam} from "react-icons/fa";
import {SiOrigin} from "@react-icons/all-files/si/SiOrigin";
import {FaXbox} from "@react-icons/all-files/fa/FaXbox";
import {CgMicrosoft} from "@react-icons/all-files/cg/CgMicrosoft";
import Minecraft from "./imagesHeader/minecraft.jpg";
import Uplay from "./imagesHeader/uplay.png"
import HeaderLiElement from "./headerLiElement";
import HeaderLiElementCategory from "./headerLiElementCategory";
import {BiCategory} from "react-icons/all";

const ButtonsStyle = ({s}) => {
    return (
        <div className={s}>
            <span className={style.itemSupport}>ПОДДЕРЖКА</span>
            <span className={style.itemPurchases}>МОИ ПОКУПКИ</span>
        </div>
    )
}



const Header = (props) => {


    return (

        <div>
            <div style={{
                padding: 0
            }} className="container-lg center">
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                    <div>
                        <NavLink className="navbar-brand" to={'/'}>ЛОГО</NavLink>
                    </div>
                    <ButtonsStyle s={style.itemsMobile}/>
                    <div className={style.paddingBurger}>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse"
                            data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false"
                            aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    </div>
                    <div className="collapse navbar-collapse" id="navbarNavDropdown">
                        <ul className="navbar-nav">

                            <HeaderLiElement link={'main'} text={'КАК КУПИТЬ'} />
                            <HeaderLiElement link={'main'} text={'ГАРАНТИИ'} />
                            <HeaderLiElement link={'main'} text={'КОНТАКТЫ'} />

                        </ul>
                    </div>

                    <ButtonsStyle s={style.itemsDesktop}/>
                </nav>
            </div>
            <div className={style.wrapTwoNavbar}>
                <div className="container-lg center">
                    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">

                        <div className="container-fluid">

                            <button className="navbar-toggler" type="button" data-bs-toggle="collapse"
                                    data-bs-target="#navbarNavDropdown2" aria-controls="navbarNavDropdown2" aria-expanded="false"
                                    aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>

                            <div className={style.searchStyle}>
                            <form className="d-flex">
                                <input className="form-control me-2" type="search" placeholder="Поиск по магазину"
                                       aria-label="Поиск"/>
                            </form>
                            </div>

                            <div className="collapse navbar-collapse" id="navbarNavDropdown2">
                                <ul className="navbar-nav">
                                    <HeaderLiElementCategory link={'catalog'} text={'Каталог'} icon={<BiCategory />} />
                                    <HeaderLiElementCategory link={'main'} text={'Minecraft'} icon={Minecraft} />
                                    <HeaderLiElementCategory link={'main'} text={'Steam'} icon={<FaSteam />} />
                                    <HeaderLiElementCategory link={'main'} text={'Origin'} icon={<SiOrigin />} />
                                    <HeaderLiElementCategory link={'main'} text={'Uplay'} icon={Uplay} />
                                    <HeaderLiElementCategory link={'main'} text={'Xbox'} icon={<FaXbox/>} />
                                    <HeaderLiElementCategory link={'main'} text={'Software'} icon={<CgMicrosoft />} />
                                </ul>
                            </div>
                        </div>
                    </nav>
                </div>
            </div>

        </div>

    )
}

export default Header;