import React from "react";
import style from "./slider.module.css";
import {NavLink} from "react-router-dom";
import {IoCart} from "react-icons/io5";

const SlideItem = ({dataItem, classNameItem}) => {

    console.log(dataItem)

    return (
        <NavLink to={`/product/${dataItem.id}`} className={classNameItem}>
            <img src={dataItem.imgItem}
                 className="d-block w-100" alt="..."/>
            <div className={style.panelItem}>
                <span className={style.countSellImg}><IoCart/></span>
                <span className={style.countSell}>{dataItem.cnt_sell}</span>
                <span className={style.priceItem}>{dataItem.price_rub} ₽</span>
            </div>
        </NavLink>
    )
}

export default SlideItem;