import React, {useEffect, useState} from "react";
import Slider from "../slider/sliderContainer";
import style from "./main.module.css";
import Items from "./itemsMainBlock/itemsContainer"
import {FaCrown} from "@react-icons/all-files/fa/FaCrown";
import {MdOutlineRecommend, TbDiscount2} from "react-icons/all";
import PreloaderComponent from "../preloader/preloader";
import LastPurchases from "./lastPurchases/lastPurchasesContainer";

const Main = (props) => {

    useEffect(() => {
        props.requestItems();
        props.requestItemsFromCategories("lead")
    }, [])

    let [initSlider, setInitSlider] = useState(props.initSlider);

    useEffect(() => {
        setInitSlider(props.initSlider)
        console.log(initSlider)
    }, [initSlider, props.initSlider])

    const category = [
        { id: "lead", text: "Лидеры продаж"},
        { id: "rec", text: "Рекомендуем" },
        { id: "dis", text: "Скидки" }
    ];
    const [activeId, setActiveId] = useState("lead");

    const onActiveCategory = (id) => {
        if (id !== activeId) {
            props.initSellLeadersAC(false)
            setActiveId(id)
            props.requestItemsFromCategories(id);
        }
    }

    return (
        <div className={style.mainWrap}>
        <div className="container-lg center">
            {initSlider && <Slider />}
            <br />
            <div className="container">
                <div className="row">
                    <div className={style.categoryWrap}>
                        {category.map((val) => (
                            <div key={val.id} className="col-lg">
                            <div onClick={() => onActiveCategory(val.id)} className={activeId === val.id ? style.menuItemActive : style.menuItem}>
                                {val.text + " "}
                                {val.id === "lead" && <FaCrown />}
                                {val.id === "rec" && <MdOutlineRecommend />}
                                {val.id === "dis" && <TbDiscount2 />}
                            </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            {props.initSellLeaders === false ? <PreloaderComponent w={160}  h={160} /> : <Items />}

            <LastPurchases />

            </div>
        </div>
    )
}

export default Main;