import React from "react";
import {NavLink} from "react-router-dom";
import style from "./header.module.css";

const HeaderLiElementCategory = ({link, text, icon}) => {
    return (
        <li className="nav-item">
            <NavLink to={`/${link}`} className="nav-link" aria-current="page">
            <span className={style.linkHoverSolid2}>
            {typeof icon === 'string' ? <img className={style.imgHeader2} src={icon} /> : icon}
            <span>{text}</span></span></NavLink>
        </li>
    )
}

export default HeaderLiElementCategory;